@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Roboto:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-size:18px}#root{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;min-height:100vh}#root>main{margin-left:350px;max-width:800px}#root>main>header h1{font-size:4.5em;margin:0;padding:0;display:block;font-family:"Roboto",sans-serif}#root>main>header h2{font-size:3em;font-family:"Bebas Neue",cursive;color:#215383}#root>main>header h1,#root>main>header h2{margin:0;padding:0}#root>main>header button{margin-right:1em}hr{margin:3em 0}section h3{font-size:1.8em}section p{font-size:1.2em;line-height:150%}article{margin-bottom:3em}.left-accented{padding-left:2em;border-left:7px solid #ff3939}.two-columns{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row}.two-columns header{width:30%;line-height:150%;padding-right:1em}.two-columns header .credentials{font-weight:bold;font-size:1.2em}.two-columns header .credential-issuer{font-size:1em;font-weight:bold;text-transform:uppercase;color:#707070;margin-bottom:1em}.two-columns p.description{width:60%;margin:0;font-size:1em}
nav{background-color:#358bdc;position:fixed;left:0;top:0;padding:2em;height:100%;width:250px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:flex-end;justify-content:flex-end}nav a,nav h2{display:block;padding:.5rem 1rem;text-align:right;margin:0;text-transform:uppercase}nav h2{font-size:1.5em}nav a{text-decoration:none;color:#fff;font-size:1.2em}nav a:hover{color:#96b6e6}

.tech-details{font-size:1rem;display:block;background-color:#e6e6e6;padding:.5em}
.three-columns{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between}.three-columns div{width:30%;padding-right:2em}.three-columns div:last-child{padding-right:0}
.link-button{padding:.5em 1em;background-color:#358bdc;display:inline-block;color:#fff;margin-right:1em;border-radius:1em 1em 1em 1em;-moz-border-radius:1em 1em 1em 1em;-webkit-border-radius:1em 1em 1em 1em}.link-button:link{text-decoration:none}.link-button:hover{background-color:#1f5486}


